import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Col, Row, Card, Form, Input } from 'antd';
import {
  createFeeSubmitSubject,
  updateFeeSubmitSubject,
  getFeeSubmitSubjectById,
} from 'services/feeSubmitSubjectType';
import { getQueryParams } from 'utils/urlUtil';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    name: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormFeeSubmitSubjectTypePage' })
class FormFeeSubmitSubjectTypePage extends Component {
  state = {
    dataId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      this.setState({ dataId: queryParamId });
      this.getDataById(queryParamId);
    }
  };

  getDataById = async id => {
    const res = await getFeeSubmitSubjectById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { dataId } = this.state;
    try {
      if (!dataId) {
        await createFeeSubmitSubject(values);
      } else {
        await updateFeeSubmitSubject({ id: dataId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { dataId } = this.state;
    return (
      <Card title={`${dataId ? 'Cập nhật' : 'Thêm'} hóa đơn`}>
        <Row>
          <Form onSubmit={this.handleSubmit}>
            <Row>
              <Col span={24}>
                <Form.Item hasFeedback label="Tên đối tượng" {...formItemLayout}>
                  {getFieldDecorator('label', {
                    initialValue: initialValue.label,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(<Input />)}
                </Form.Item>
              </Col>
            </Row>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormFeeSubmitSubjectTypePage {...props} />
  </Page>
));



